/*------------- #UI-BLOCK --------------*/

.container {
  width: auto;
  margin-left: 270px;
}

.container-content {
  margin: 50px;
  padding: 50px;
  background-color: #fff;
}

.chapter-overview-desc {
  display: flex;
  & .btn {
    margin-bottom: 0px;
    margin-right: 10px;
  }
}

.chapter-overview-label {
  font-size: 24px;
  color: #bdc0cd;
  line-height: 50px;
  display: inline-block;
  height: 50px;
}

.file-uploads-user {
  display: flex;
  align-items: center;
  color: #0e1842;
  font-weight: bold;

  &>a {
    margin-right: 5px;
  }
}
.form-group.label-floating .form-control button {
  padding: 1.5rem 1.1rem .5rem !important;
}

.setings-container .control-label {
  left: 1.1rem !important;
}

.ui-block {
  background-color: $white-color;
  border-radius: 5px;
  border: 1px solid $border-color;
  margin-bottom: 15px;

  .ui-block-title:last-child {
    border-bottom: 0;
  }
}

.ui-block-title {
  padding: 23px 25px 18px;
  line-height: 1;
  border-bottom: 1px solid $border-color;
  border-top: 1px solid $border-color;
  display: table;
  width: 100%;
  position: relative;
  border-radius: 5px 5px 0 0;

  .more {
    padding-left: 25px;
    text-align: right;
    width: 16px;
  }

  & > * {
	margin-bottom: 0;
    display: table-cell;
    vertical-align: middle;
  }

  &.ui-block-title-small {
    padding: 10px 25px;
    .title {
      font-size: 9px;
      text-transform: uppercase;
      color: #9a9fbf;
    }
  }

  &:first-child {
    border-top: none;
  }

  .btn {
    display: inline-block;
    margin-bottom: 0;

    & + * {
      margin-left: 20px;
    }

  }

  &.inline-items {
    .more {
      float: right;
      margin-top: 6px;
      padding-left: 0;
    }
  }

  & + .ui-block-title {
    border-bottom: none;
  }

  .icon-add {
    margin-right: 0;
  }

}

#accordion + .ui-block-title {
  border-top: none;
  border-bottom: none;
}

.ui-block-content {
  padding: 24px 23px 23px;
}



/*------------- Responsive mode styles --------------*/

@media (max-width: 1200px) {
  .responsive-flex1200 {

    .ui-block-title {
      display: flex;
      flex-direction: column;

      & > * {
        margin-bottom: 10px;
      }

      .points {
        text-align: left;
      }
    }

    .btn + * {
      margin-left: 0;
    }

    .w-search {
      width: auto;
    }

    .w-select {
      padding: 0;
    }
  }
}

@media (max-width: 860px) {
  .responsive-flex {

    .w-search {
      width: auto;
    }
  }
}

@media (max-width: 768px) {
  .responsive-flex {

    .ui-block-title  {
      display: flex;
      flex-direction: column;

      & > * {
        margin-bottom: 10px;
      }

      .points {
        text-align: left;
      }
    }

    .block-btn {
      text-align: left;
      padding-right: 0;
    }

    .more {
      padding-left: 0;
    }

    .photo-gallery {
      position: relative;
      right: auto;
      order: 5;
      left: -26px;
      bottom: -29px;
      top: auto;

      &.nav-tabs .nav-item {
        border-top: 1px solid $border-color;
        text-align: center;
      }
    }

    .w-search {
      width: auto;
    }

    .bootstrap-select.without-border.btn-group .dropdown-toggle .filter-option {
      text-align: left;
    }

    .align-right {
      text-align: left;

      * {
        display: block;
      }

       & > * + * {
        margin-top: 15px;
      }

      .btn {
        margin-left: 0;
      }
    }

    .ui-block-title .btn + * {
      margin-left: 0;
    }
  }
}


@media (max-width: 480px) {
  .ui-block-content.display-flex.content-around {
    flex-direction: column;

    & > * {
      margin-bottom: 15px;
    }
  }
}

@media (max-width: 320px) {
  .ui-block-title .btn + .btn {
    margin-left: 0;
    margin-top: 15px;
  }
}


